import React, { Component} from 'react';
import Card from './Card';
import Controller from "./Controller";
import strings from "../../Localisation";
import nextId from "react-id-generator";
import {sendAmplitudeData} from "../../../utils/Amplitude";
const MEDIA_CARD_MIN_WIDTH = 300;
const MARGIN_MEDIA_CARD = 40;

export default class Media extends Component {
    constructor(props) {
        super(props);

        this.leftControllerId = nextId();
        this.rightControllerId = nextId();
        this.state = {
            cards_number: 3,
            current_index: 0
        };
    }

    componentDidMount() {
        let leftController = document.getElementById(this.leftControllerId);
        let rightController = document.getElementById(this.rightControllerId);
        let link = this;

        leftController.addEventListener('click', function (event) {
            event.preventDefault();
            link.updateCardIndex(-1);
        });

        rightController.addEventListener('click', function (event) {
            event.preventDefault();
            link.updateCardIndex(1);
        });

        window.addEventListener("resize", function(event) {
            link.resetCardsNumber()
        });

        this.resetCardsNumber();
    }

    updateCardIndex(direction) {
        let index = (this.state.current_index + direction) % this.getAvailableCards().length;

        if (index < 0) {
            index = this.getAvailableCards().length - 1;
        }

        sendAmplitudeData("Media: swiped card on " + direction);

        console.log("current_index:" + index);
        this.setState({ current_index: index });
    }

    resetCardsNumber() {
        let mediaContent = document.getElementsByClassName('media')[0].offsetWidth - document.getElementsByClassName('media_controller')[0].offsetWidth * 2;
        let number = parseInt(mediaContent / (MEDIA_CARD_MIN_WIDTH + MARGIN_MEDIA_CARD), 10);
        if (number > this.getAvailableCards().length) {
            number = this.getAvailableCards().length;
        }

        number = (number === 0)? 1 : number;

        if (this.state.cards_number !== number) {
            this.setState({ cards_number: number })
        }
    }

    getCards() {
        let link = this;

        let indecesArray = [];

        for (let i = 0; i < link.state.cards_number; i++) {
            let localIndex = (link.state.current_index + i) %  link.getAvailableCards().length;

            if (localIndex < 0) {
                localIndex = link.getAvailableCards().length - 1;
            }

            indecesArray[indecesArray.length] = localIndex;
        }

        return indecesArray.map(function callback(currentValue, index, array) {
            return link.getAvailableCards()[currentValue];
        })
    }

    getAvailableCards() {
        return [
            <Card
                description={"Меня всегда интересовал вопрос: кто пользуется функцией управления глазами в современных смартфонах? Постоянно смотреть в камеру ..."}
                src={ "/images/main/media/vcru.png"}
                path={"https://rozetked.me/articles/7192-rossiyskiy-razrabotchik-realizoval-upravlenie-smartfonom-mimikoy-reface"}
                title={"Reface — управляй миром движением глаз"}/>,

            <Card
                description={"Не так давно на нашем YouTube-канале выходил ролик  о Google Pixel 4, одной из главных фишек которого должна стать технология Project Soli ..."}
                path={"https://rb.ru/interview/so-mnoj-konkuriruet-ilon-mask/"}
                src={ "/images/main/media/rozetked.png"}
                title={"Проект Reface — управление смартфоном мимикой ..."}/>,

            <Card
                description={"Денис Шилов учится в ВШЭ  на втором курсе факультета прикладного анализа данных и работает Android-разработчиком. А еще он разработал проект ..."}
                path={"https://vc.ru/tribuna/66577-reface-upravlyay-mirom-dvizheniem-glaz"}
                src={ "/images/main/media/rusbase.png"}
                title={"Со мной конкурирует Илон Маск"}/>,

            <Card
                description={"Русский разработчик Денис Шилов придумал устройство, которое управляет телефоном при помощи электрических сигналов мозга и мышц ..."}
                path={"https://t.me/thecodemedia/582"}
                src={ "/images/main/media/kod.png"}
                title={"Управлять смартфоном в одно касание — прошлый век"}/>,

            <Card
                description={"Причём работает это не через камеру — специальное устройство считывает активность мозга и передаёт в приложение ..."}
                path={"https://t.me/brodetsky/1632"}
                src={ "/images/main/media/brodetsky.png"}
                title={"Мне написал Денис Шилов, парень с интересной разработкой"}/>,

            <Card
                description={"Оно уже работает с готовой гарнитурой, сейчас предприниматель ищет средства, чтобы создать комплект из гарнитуры"}
                path={"https://openmedia.io/wtfuture/prilozhenie-pomogaet-upravlyat-smartfonom-dvizheniyami-glaz/"}
                src={ "/images/main/media/openmedia.png"}
                title={"Российский программист создал приложение для управления смартфоном"}/>
        ]
    }

    render() {
        return (
            <div>
                <div className={"media_up_space"}/>
                <div className={"media_title"}>{strings.media__title}</div>
                <div className={"media"}>
                    <Controller isLeft={true} id={this.leftControllerId}/>

                    <div className={"media_content"}>
                        {this.getCards()}
                    </div>

                    <Controller isLeft={false} id={this.rightControllerId}/>
                </div>

                <div className={"media_down_space"}/>
            </div>
        )
    }
}